/* Single posts navigation styles - begin
    ====================================================================== */

.eltd-blog-single-navigation {
    position: relative;
    display: table;
    width: 100%;
    vertical-align: middle;
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .eltd-blog-single-navigation-inner {
        padding: 65px 0 25px;

        .eltd-custom-prev-icon,
        .eltd-custom-next-icon {
            position: relative;
            z-index: 3;
            color: $default-heading-color;
            @include eltdTransform(translateY(-50%));
            @include eltdTransition(color .2s ease-out);

            &:hover {
                color: $default-info-color;
            }

        }

        .eltd-custom-prev-icon {
            margin: 13px 13px 13px 0;
        }
        .eltd-custom-next-icon {
            margin: 13px 0 13px 13px;
        }

        .custom-line-top,
        .custom-line-bottom {
            position: relative;
            display: block;
            height: 10px;
            width: 1px;
            background-color: $default-heading-color;
            @include eltdTransition(background-color 0.2s cubic-bezier(0.32, 0.93, 0.49, 0.77), transform 0.2s cubic-bezier(0.32, 0.93, 0.49, 0.77), -webkit-transform 0.2s cubic-bezier(0.32, 0.93, 0.49, 0.77));
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }

        .custom-line-top {
            bottom: -2px;
        }

        .custom-line-bottom {
            top: -2px;
        }

    }

    .eltd-blog-single-prev:hover {
        color: $default-heading-color;
        .custom-line-top {
            @include eltdTransform(rotate(45deg));
        }
        .custom-line-bottom {
            @include eltdTransform(rotate(-45deg));
        }

    }

    .eltd-blog-single-next:hover {
        color: $default-heading-color;
        .custom-line-top {
            @include eltdTransform(rotate(-45deg));
        }
        .custom-line-bottom {
            @include eltdTransform(rotate(45deg));
        }
    }

    .eltd-blog-single-prev {
        float: left;

        .eltd-custom-prev-icon,
        .eltd-blog-single-nav-label {
            float: left;
            text-transform: uppercase;
            font-size: 12px;
            letter-spacing: 1.5px;
            color: $default-text-color;
            font-weight: 400;
        }

    }

    .eltd-blog-single-next {
        float: right;

        .eltd-custom-next-icon,
        .eltd-blog-single-nav-label {
            float: right;
            text-transform: uppercase;
            font-size: 12px;
            letter-spacing: 1.5px;
            color: $default-text-color;
            font-weight: 400;
        }

    }
}

.eltd-single-links-pages {
    clear: both;
    margin: 20px 0 6px;

    .eltd-single-links-pages-inner {

        > a,
        > span {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0 15px 5px 0;
        }

        > span {
            &:after {
                content: '';
                border-bottom: 1px solid $default-menu-color;
                width: 150%;
                position: absolute;
                bottom: 3px;
                left: -25%;
            }
        }
    }
}

/* Single posts navigation styles - end
    ====================================================================== */