/* ==========================================================================
   Blog Common style - begin
   ========================================================================== */

.eltd-blog-holder {
    @include eltdRelativeHolderLayout();
    margin: 0 0 40px;
    clear: both;

    article {
        @include eltdRelativeHolderLayout();
        box-sizing: border-box;

        &.sticky {

            .eltd-post-title a {
                color: $first-main-color;
            }
        }

        .eltd-post-content {
            @include eltdRelativeHolderLayout();
        }

        .eltd-post-image {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            max-width: 100%;

            img {
                display: block;
            }
        }

        .eltd-post-image-background {
            @include eltdRelativeHolderLayout();
            height: 100%;
            background-position: center;
            background-size: cover;

            a {
                @include eltdAbsoluteHolderLayout();
            }
        }

        .eltd-post-image-icon {
            position: absolute;
            top: 20px;
            right: 20px;
            font-size: 30px;
            line-height: 1;
            color: #fff;
        }

        .eltd-post-text {
            @include eltdRelativeHolderLayout();
            box-sizing: border-box;
        }

        .eltd-post-title {
            margin: 0 0 13px 0;
        }

        .eltd-post-excerpt-holder {
            @include eltdRelativeHolderLayout();
            margin: 4px 0 17px 0;

            p {
                margin: 0;
            }
        }

        .eltd-post-excerpt {

            &:after {
                content: '...';
            }
        }

        .eltd-more-link-container {
            @include eltdRelativeHolderLayout();
            margin: 15px 0 0;
        }

        .eltd-post-info {
            @include eltdRelativeHolderLayout();

            > * {
                position: relative;
                display: inline-block;
                vertical-align: top;
                color: currentColor;

                a {
                    color: inherit;
                }
            }
        }

        .eltd-post-info-top  {

            > div {
                position: relative;
                display: inline-block;
                vertical-align: top;
                padding: 0 6px 0 0;
                color: $default-text-color;
                font-size: 11px;
                letter-spacing: .22em;
                text-transform: uppercase;
                font-weight: 500;
                margin: 0 2px 0 0;

                &:after {
                    content: '/';
                    position: absolute;
                    top: 0;
                    right: -2px;
                }

                &:last-child:after {
                    content: '';
                    position: absolute;
                    top: 0;
                    right: -2px;
                }

                a {
                    color: inherit;

                    &:hover {
                        color: $default-menu-color;
                    }
                }
            }
        }

        .eltd-post-info-bottom {



            .eltd-post-info-bottom-left,
            .eltd-post-info-bottom-right {
                @include eltdRelativeHolderLayout();
            }

	        @media only screen and (min-width: $ipad-portrait-plus-pixel) {

		        .eltd-post-info-bottom-left,
		        .eltd-post-info-bottom-right {
			        width: 50%;
		        }


                .eltd-post-info-bottom-left a {
                    color: $default-info-color;

                     &:hover{
                        color: $default-menu-color;
                    }
                }

		        .eltd-post-info-bottom-right {
			        text-align: right;
			        float: right;
                        margin-top: 2px;
		        }
	        }

            .eltd-post-info-bottom-right {

                > div {
                    margin-left: 17px;

                    &:first-child {
                        margin-left: 0;
                    }
                }
            }
        }

        .eltd-tags-holder {
            font-size: 11px;
            letter-spacing: .1em;
            text-transform: uppercase;
            font-weight: 500;
        }

        //Link post style
        &.format-link {

            .eltd-post-text{
                padding: 47px 121px 47px 121px;
                border: 1px solid $default-border-color;
            }

            .eltd-post-text-main {
                text-align: center;
            }


            .eltd-post-link-holder {
                vertical-align: top;
                text-align: center;
            }

            .eltd-post-excerpt-holder{
                margin: 15px 0 16px 0;
            }

            .eltd-post-link-holder a{
                @include eltdAbsoluteHolderLayout();
            }


        }

        //Quote post style
        &.format-quote {

            .eltd-post-text {
                padding: 55px 90px 42px 90px;
                border: 1px solid $default-border-color;
            }


            .eltd-post-text-main {
                text-align: center;

            }

            .eltd-post-excerpt-holder{
                margin: 15px 0 16px 0;
            }

        }

        //Gallery post style
        &.format-gallery {

            .eltd-post-image {
                width: 100%;
            }

            .owl-carousel .owl-item {
                z-index: 2;

                &.active {
                    z-index: 3;
                }
            }
        }

        //Audio post style
        &.format-audio {

            &.has-post-thumbnail {

                .eltd-post-heading {
                    position: relative;
                    margin-bottom: 50px;
                }

                .eltd-blog-audio-holder {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    display: inline-block;
                    vertical-align: middle;
                }
            }

            .eltd-post-image {
                width: 100%;
                max-width: inherit;

                img {
                    width: 100%;
                }
            }

            .eltd-blog-audio-holder {

                .mejs-container, .mejs-embed, .mejs-embed body, .mejs-container .mejs-controls {
                    background: none;
                }

                .mejs-container {
                    background-color: $default-heading-color;
                }

                .mejs-container,
                .mejs-container .mejs-controls {
                    height: 38px !important;
                }

                .mejs-container .mejs-controls {
                    padding: 0 10px 0 4px;
                    box-sizing: border-box;

                    .mejs-duration-container{
                        margin-left: 22px;
                    }
                }

                .mejs-container .mejs-controls > .mejs-playpause-button {
                    width: 39px;
                }

                .mejs-container .mejs-controls > .mejs-volume-button {
                    width: 30px;
                }

                .mejs-container .mejs-controls > .mejs-playpause-button,
                .mejs-container .mejs-controls > .mejs-volume-button {
                    height: 38px;

                    button {
                        position: relative;
                        height: 100%;
                        background: none;
                        background-position: center;
                        background-repeat: no-repeat;
                        margin: 0;
                        border: 0;
                        outline: none;
                    }

                    &.mejs-play button {
                        width: 39px;
                        background-image: url('../img/audio/play.png');
                    }

                    &.mejs-pause button {
                        width: 39px;
                        background-image: url('../img/audio/pause.png');
                    }

                    &.mejs-stop button {
                        width: 39px;
                        background-image: url('../img/audio/stop.png');
                    }

                    &.mejs-mute button {
                        width: 30px;
                        background-image: url('../img/audio/sound.png');
                    }

                    &.mejs-unmute button {
                        width: 30px;
                        background-image: url('../img/audio/mute.png');
                    }
                }

                .mejs-container .mejs-controls > .mejs-time {
                    height: 100%;
                    padding: 0;
                    font-family: inherit;
                    font-size: 13px;
                    color: #fff;
                    line-height: 36px;
                }

                .mejs-container .mejs-controls > .mejs-time-rail {
                    height: 100%;
                    padding: 0;
                    font-family: inherit;

                    .mejs-time-total {
                        height: 5px;
                        margin: 15px 14px;
                        background: none;
                        background-color: #fff;

                        .mejs-time-current {
                            height: 5px;
                            background-color: #545454;
                        }

                        .mejs-time-float {
                            top: -17px;
                            border: 0;
                            color: #fff;

                            .mejs-time-float-corner {
                                border-top-color: $default-heading-color;
                            }
                        }

                        .mejs-time-loaded,
                        .mejs-time-float {
                            background: none;
                        }

                        .mejs-time-loaded{
                            height: 5px;
                        }

                        .mejs-time-handle{
                            top:0px;
                            .mejs-time-handle-content{
                                top:-7px;
                            }
                        }

                        .mejs-time-hovered{
                            height: 0px;
                        }
                    }
                }

                .mejs-container .mejs-controls > a.mejs-horizontal-volume-slider {
                    height: 38px;

                    .mejs-horizontal-volume-total,
                    .mejs-horizontal-volume-current {
                        height: 5px;
                        background: none;
                    }

                    .mejs-horizontal-volume-total {
                        background-color: #fff;
                    }

                    .mejs-horizontal-volume-current {
                        background-color: $default-text-color;
                    }
                }
            }
        }

        &.format-video {

            .eltd-self-hosted-video {
                max-width: 100%;
            }
        }
    }

}

/* ==========================================================================
   Blog Common style - end
   ========================================================================== */