/* ==========================================================================
   Blog List - Masonry Gallery style - begin
   ========================================================================== */

.eltd-blog-holder {
    
    &.eltd-blog-masonry-gallery {
	
	    article {
            opacity:0;
		    @include eltdTransition(opacity 0.15s ease-out);

            &.eltd-appeared {
                opacity:1;
            }
		
		    &:hover {
			
			    .eltd-post-text {
				    @include eltdTransitionTransform(.5s cubic-bezier(.44, 1.22, 0, 1.43));
				    @include eltdTransform(translateY(-20px));
			    }
		    }

            .eltd-post-content {
                height: 100%;
            }
		
		    .eltd-post-image {
			    height: 100%;
			    
			    img {
				    height: 100%;
			    }
			    
			    &:after {
				    @include eltdAbsoluteHolderLayout();
				    content: '';
				    background: rgba($default-heading-color, 0.62);
				    background: -webkit-linear-gradient(rgba($default-heading-color, 0), rgba($default-heading-color, 0.62));
				    background: -moz-linear-gradient(rgba($default-heading-color, 0), rgba($default-heading-color, 0.62));
				    background: linear-gradient(rgba($default-heading-color, 0), rgba($default-heading-color, 0.62));
			    }
		    }
		    
		    .eltd-post-text {
			    position: absolute;
			    bottom: 39px;
			    left: 0;
			    padding: 0 20px;
			    text-align: center;
			    box-sizing: border-box;
			    @include eltdTransitionTransform(.3s cubic-bezier(.44, 1.22, 0, 1.43));
			    
			    a {
				    position: relative;
				    z-index: 2;
			    }
		    }
		    
		    .eltd-post-title,
			.eltd-post-excerpt,
            .eltd-post-info {
			    color: #fff;
		    }
		
		    &.format-link,
		    &.format-quote {
			
			    .eltd-post-text {
				    background-color: transparent;
			    }
		    }
	    }
    }
}
/* ==========================================================================
   Blog List - Masonry Gallery style - end
   ========================================================================== */