/* ==========================================================================
   Blog List - Masonry style - begin
   ========================================================================== */

.eltd-blog-holder {

    &.eltd-blog-masonry {

        &.eltd-tiny-space .eltd-outer-space .eltd-item-space {
            margin: 0 0 20px;
        }
        &.eltd-small-space .eltd-outer-space .eltd-item-space {
            margin: 0 0 35px;
        }
        &.eltd-normal-space .eltd-outer-space .eltd-item-space {
            margin: 0 0 45px;
        }
        &.eltd-medium-space .eltd-outer-space .eltd-item-space {
            margin: 0 0 55px;
        }
        &.eltd-large-space .eltd-outer-space .eltd-item-space {
            margin: 0 0 70px;
        }
        &.eltd-huge-space .eltd-outer-space .eltd-item-space {
            margin: 0 0 100px;
        }

        .eltd-post-content {
            .eltd-post-image {
                overflow:hidden;

                img {
                    @include eltdTransition(all .35s cubic-bezier(.32,.93,.49,.77) 50ms);
                }
            }

            &:hover {
                .eltd-post-image {
                    img {
                        @include eltdTransform(scale(1.05));
                    }
                }
            }
        }

        article {
            margin: 0 0 20px;
            padding: 0 0 30px;

            &.eltd-post-has-media {

                .eltd-post-heading {
                    margin-bottom: 30px;
                }
            }

            &.format-link .eltd-post-text {
                padding: 60px 33px;
                .eltd-link-title {
                    word-wrap: break-word;
                }

            }
            &.format-quote .eltd-post-text {
                padding: 60px 33px;

                .eltd-quote-author {
                    letter-spacing: 2px;
                }
            }



            .eltd-post-title{
                margin:  5px 0 0 0;

            }

        }

        .eltd-post-excerpt-holder {
            margin: 17px 0 16px 0;
        }


        .eltd-post-quote-holder a, .eltd-post-link-holder a {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
        }



    }
}

/* ==========================================================================
   Blog List - Masonry style - end
   ========================================================================== */