/* ==========================================================================
   Blog List - Standard style - begin
   ========================================================================== */

.eltd-blog-holder {
	
    &.eltd-blog-standard {
	    
        article {
            margin: 0 0 31px;
            padding: 0;

            .eltd-post-text-main{
                margin-bottom: 2px;
            }

            &.eltd-post-has-media {
	            

                .eltd-post-heading {
                    margin-bottom: 40px;
                }

                .eltd-post-quote-holder a,.eltd-post-link-holder a {
                  position:absolute;
                  top:0;
                  right:0;
                  bottom:0;
                  left:0;
                   }

            }
            .eltd-tags-holder,.eltd-blog-share{
                margin-top: 10px;
            }

            &.format-quote {
                .eltd-post-content{
                    margin-bottom: 28px;
                }
                .eltd-quote-author{
                    letter-spacing: 2px;
                }

                .eltd-post-quote-holder a {
                    z-index: 20;
                    display: block;
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    top: 0;
                    left: 0;
                }
                .eltd-post-quote-holder:hover .eltd-post-quote-holder-inner {
                        opacity:0.8;
                        transition: opacity 0.2s ease-out;
                }
            }
            &.format-link{
                .eltd-post-content{
                    margin-bottom: 28px;
                }

                .eltd-post-text:hover .eltd-post-text-main  {
                    transition: opacity 0.2s ease-out;
                    opacity:0.8;
                }
            }


        }


    }
}

/* ==========================================================================
   Blog List - Standard style - end
   ========================================================================== */

