/* ==========================================================================
   Blog List shortcode - Masonry type style - begin
   ========================================================================== */

.eltd-blog-list-holder {
	
	&.eltd-bl-masonry {
		transition: none !important;
		overflow: visible !important;

        &.eltd-tiny-space .eltd-outer-space .eltd-item-space {
            margin: 0 0 30px;
        }
		&.eltd-small-space .eltd-outer-space .eltd-item-space {
            margin: 0 0 50px;
        }
		&.eltd-normal-space .eltd-outer-space .eltd-item-space {
            margin: 0 0 60px;
        }
		&.eltd-medium-space .eltd-outer-space .eltd-item-space {
            margin: 0 0 70px;
        }
        &.eltd-large-space .eltd-outer-space .eltd-item-space {
            margin: 0 0 85px;
        }
        &.eltd-huge-space .eltd-outer-space .eltd-item-space {
            margin: 0 0 120px;
        }

		.eltd-blog-list {
			opacity: 0;
			@include eltdTransition(opacity .2s ease-out);
		}
		
		.eltd-bl-item {
			vertical-align: top;
			z-index: 100;
		}

        .eltd-post-image {
            margin: 0 0 33px;
        }

        .eltd-post-excerpt {
            margin: 17px 0 0;
        }

        .eltd-post-read-more-button {
            margin: 17px 0 0;
        }
		
		.eltd-bl-item,
		.eltd-bl-grid-sizer {
			width: 100%;
		}
		
		.eltd-bl-grid-gutter {
			width: 0;
		}
		
		$masonry_columns_number: ('two', 'three', 'four', 'five');
		
		@for $i from 0 to length($masonry_columns_number) {
			&.eltd-bl-#{nth($masonry_columns_number,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.eltd-bl-item,
				.eltd-bl-grid-sizer {
					width: $column_width;
				}
			}
		}
	}
}
/* ==========================================================================
   Blog List shortcode - Masonry type style - end
   ========================================================================== */