/* ==========================================================================
   Blog Single style - begin
   ========================================================================== */

.eltd-blog-holder.eltd-blog-single {

	article {
		margin: 0 0 30px;

		&.format-link{
			.eltd-post-text{
				margin-bottom: 28px;
			}

			.eltd-post-text:hover .eltd-post-text-main  {
				transition: opacity 0.2s ease-out;
				opacity:0.8;
			}
		}
		&.format-quote{
			.eltd-post-text{
				margin-bottom: 28px;
				.eltd-quote-author{
					letter-spacing: 2px;
				}
			}

			.eltd-post-text:hover .eltd-post-text-main{
				transition: opacity 0.2s ease-out;
				opacity:0.8;
			}
		}

		.eltd-post-heading {
			margin-bottom: 42px;
		}
	}

  	.eltd-post-info-bottom{
		padding-top: 46px;
		padding-bottom: 30px;
	}
}
/* ==========================================================================
   Blog Single style - end
   ========================================================================== */