/* Single author box styles - begin
	====================================================================== */

.eltd-author-description {
    @include eltdRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltd-author-description-inner {
        padding: 52px 0 50px;
        min-height: 78px; // static height of author image -- eltd-author-description-image
        clear: both;
        border-bottom: 1px solid #e1e1e1;
        border-top: 1px solid #e1e1e1;
    }

    .eltd-author-description-content {
        position: relative;
        padding: 0 30px;
    }

    .eltd-author-description-image {
        width: 120px;
        margin: 0 auto;
        float: left;

        img {
            display: block;
            border-radius: 5em;
        }
    }

    .eltd-author-description-text-holder {
        position: relative;
        padding: 0px 0 0 143px;
        min-height: 120px;

        .eltd-author-name {
            margin: 0;

            a {
                color: $default-text-color;
                font-weight: 300;

                &:hover {
                    opacity:0.85;
                }
            }
        }

        .eltd-author-email {
            margin: 0;
        }

        .eltd-author-text {
            @include eltdRelativeHolderLayout();

            > p {
                margin: 0px 0 10px;
            }
        }

        .eltd-author-social-icons {
            @include eltdRelativeHolderLayout();

            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                padding: 0;
                margin: 0 15px 0 0;
                font-size: 11px;
                color: $default-heading-color;

                &:last-child {
                    margin: 0;
                }

                &:hover {
                    opacity:0.85;
                }
            }
        }
    }

    @include ipad-portrait {
        .eltd-author-description-content {
            padding: 0;
        }
        .eltd-author-description-image {
            display: block;
            width: 100%;
            margin-bottom: 17px;
        }
        .eltd-author-description-text-holder {
            display: block;
            padding: 0;
        }
    }
}

/* Single author box styles - end
	====================================================================== */