/* ==========================================================================
   Blog List shortcode global style - begin
   ========================================================================== */

.eltd-blog-list-holder {
	@include eltdRelativeHolderLayout();
	clear: both;
	
	.eltd-blog-list {
		@include eltdRelativeHolderLayout();
		list-style: none;
		margin: 0;
		padding: 0;
	}
	
	.eltd-bl-item {
		@include eltdRelativeHolderLayout();
		padding: 0;
		margin: 0;
		box-sizing: border-box;
	}
	
	.eltd-bli-inner {
		@include eltdRelativeHolderLayout();
	}
	
	.eltd-bli-content {
		@include eltdRelativeHolderLayout();
		box-sizing: border-box;
	}
	
	.eltd-post-image {
		@include eltdRelativeHolderLayout();
		margin: 0 0 36px;
		overflow:hidden;
		
		a {
			display: block;
			@include eltdImageOverlayHoverStyle();
			@include eltdTransition(all .35s cubic-bezier(0.32, 0.93, 0.49, 0.77) 0.05s);
			@include eltdTransform(translateZ(0));
		}
		
		img {
			display: block;
		}

		&:hover {
			a {
				opacity:1;
				@include eltdTransform(scale(1.05) translateZ(0));
			}
		}
	}
	
	.eltd-bli-info {
		@include eltdRelativeHolderLayout();
		margin: 0 0 8px;
		clear: both;
        width: auto;
		
		> div {
			position: relative;
			display: inline-block;
			vertical-align: top;
			font-size: 11px;
			line-height: 1.2;
			float: left;
			padding: 0 0 5px 0;
			text-transform: uppercase;
			letter-spacing: 0.22em;
			font-weight: 400;

			&:not(:last-child):after{
				content:"/";
				padding:0 5px 0 5px;
			}

			&:last-child {
				padding: 0 0 5px;
			}

			
			a {
				color: $default-text-color;
				
				&:hover {
					opacity:0.85;
				}
			}
		}
	}
	
	.eltd-post-title {
		margin: 0;
	}
	
	.eltd-post-excerpt {
		@include eltdRelativeHolderLayout();
		margin: 18px 0 0;
		
		p {
			margin: 0;
		}
		
		&:after {
			content: '...';
		}
	}
	
	.eltd-post-read-more-button {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		margin: 22px 0 0;
	}

	.eltd-blog-share{
		display:inline-block;
		float:right;
		margin: 15px 0 0;
	}

	/************Blog List shortcode light skin style - start *******************/

	&.eltd-bl-skin-light {

		.eltd-bli-info > div {
			a {
				color: #ffffff;
			}
			&:not(:last-child):after {
				color: #ffffff;
			}
		}

		.eltd-post-info-author-text {
			color: #ffffff;
		}
		.eltd-post-read-more-button {
			.eltd-btn.eltd-btn-simple {
				color: #ffffff !important;
			}
		}

		.eltd-social-share-holder.eltd-dropdown .eltd-social-share-dropdown-opener, .eltd-post-title, .eltd-post-excerpt {
			color: #ffffff;
		}
		.eltd-social-share-holder.eltd-dropdown .eltd-social-share-dropdown-opener:hover {
			opacity:0.85;
		}

        .eltd-btn.eltd-btn-simple::after {
            background-color: #fff;
        }

	}
	/************Blog List shortcode light skin style - end  *******************/


}
/* ==========================================================================
   Blog List shortcode global style - end
   ========================================================================== */